<template>
  <b-card
    fluid
    class="pb-2 px-2 pt-1"
  >
    <b-row class="border-bottom">
      <b-col
        cols="12"
      >
        <h3 class="text-primary">
          <b-badge variant="primary" /><feather-icon
            icon="LayersIcon"
            size="16"
          />  Actividades Culturales de Recreacin
        </h3>
      </b-col>

    </b-row>
    <br>
    <!-- User Interface controls -->
    <b-row class="my-1">
      <!-- Per Page -->
      <b-col
        cols="12"
        class="mb-1 mb-md-0"
      >
        <b-row>
          <b-col cols="6">
            <b-button
              size="sm"
              variant="outline-primary"
              class="mb-1"
              :to="{ name: 'actividad-deportiva-nuevo', params: { id: 0 }}"
            >
              <feather-icon icon="PlusCircleIcon" /> Registrar nueva actividad cultural
            </b-button>
          </b-col>
          <b-col
            cols="6"
            class=""
          />
        </b-row>

      </b-col>

    </b-row>
    <b-row>
      <b-col
        cols="5"
        class=""
      >
        <label>Mostrar</label>
        <b-form-select
          id="per-page-select"
          v-model="perPage"
          :options="pageOptions"
          size="sm"
          class="per-page-selector d-inline-block mx-50"
          style="width:auto"
          @change="actualizar_to_from"
        />
        <label>registros</label></b-col>

      <b-col
        cols="7"
        class="mb-1"
      ><b-form-group
        label=""
        label-for="filter-input"
        label-cols-sm="3"
        label-align-sm="right"
        class="mb-0"
      >
        <b-input-group>
          <b-form-input
            id="filter-input"
            v-model="filter"
            type="search"
            placeholder="Buscar..."
            size="sm"
          />
          <b-input-group-append>
            <b-button
              :disabled="!filter"
              variant="success"
              size="sm"
              @click="filter = ''"
            >
              Limpiar
            </b-button>
          </b-input-group-append>
        </b-input-group>
      </b-form-group>

      </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table
      :items="actividadesDeportivas"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      stacked="sm"
      head-variant="light"
      class="position-relative"
      show-empty
      small
      hover
      responsive
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      @filtered="onFiltered"
    >
      <!-- <template #cell(name)="row">
        {{ row.value.first }} {{ row.value.last }}
      </template> -->

      <template #cell(acciones)="row">
        <b-dropdown
          variant="link"
          no-caret
          :right="$store.state.appConfig.isRTL"
        >
          <template #button-content>
            <feather-icon
              icon="MoreVerticalIcon"
              size="16"
              class="align-middle text-body"
            />
          </template>
          <b-dropdown-item
            :to="{ name: 'actividad-deportiva-nuevo', params: { id: row.item.id } }"
          >
            <feather-icon icon="EditIcon" />
            <span class="align-middle ml-50">Editar</span>
          </b-dropdown-item>
          <b-dropdown-item
            @click="eliminarActividad(row.item)"
          >
            <feather-icon icon="TrashIcon" />
            <span class="align-middle ml-50">Eliminar</span>
          </b-dropdown-item>
        </b-dropdown>
      </template>
      <template #cell(estado)="row">
        <b-badge
          pill
          :variant="`light-${resolveCitaStatusVariant(row.item.estado)}`"
        >
          {{ row.item.estado === 1 ? 'Activo' : 'Inactivo' }}
        </b-badge>
      </template>

    </b-table>
    <b-row
      cols="12"
      class="d-flex justify-content-between mb-1 mb-md-0"
    >
      <b-col cols="6">
        <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
      </b-col>
      <b-col cols="4">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
          @input="actualizar_to_from"
        />
      </b-col>

    </b-row>

  </b-card>
</template>

<script>
import {
  BCard, BDropdown, BDropdownItem, BBadge, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'

import { onUnmounted } from '@vue/composition-api'
import { mapState } from 'vuex'
import actDeportivasStoreModule from './actDeportivasStoreModule'

export default {

  components: {

    BButton,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BFormInput,
    BCard,
    BRow,
    BCol,
    BInputGroup,
    BFormSelect,
    BPagination,
    BTable,
    BInputGroupAppend,
    BBadge,
  },

  data() {
    return {
      // id: this.$attrs.id,
      items: [],
      totalRows: 0,
      from: 0,
      to: 0,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
    }
  },
  computed: {
    actualizarvar() {
      console.log('currentPage= ', this.currentPage)
      console.log('perPage= ', this.perPage)
      return console.log('actualizado')
    },
    ...mapState('actividades_depor', ['actividadesDeportivas', 'tabla']),
  },
  mounted() {
    store.dispatch('actividades_depor/fetchActividades').then(() => {
      this.totalRows = store.state.actividades_depor.actividadesDeportivas.length
      this.actualizar_to_from()
    })
  },
  methods: {
    refreshTabla() {
      store.dispatch('actividades_taller/fetchActividades').then(response => {
        this.totalRows = response.data.data.length
        this.actualizar_to_from()
      })
    },
    actualizar_to_from() {
      const { totalRows } = this
      if (totalRows === 0) {
        this.from = 0
        this.to = 0
      } else
      if (this.currentPage === 1) {
        this.from = 1
        if (totalRows <= this.perPage) {
          this.to = totalRows
        } else {
          this.to = this.perPage
        }
      } else {
        this.from = ((this.currentPage - 1) * this.perPage) + 1
        if ((this.currentPage * this.perPage) <= totalRows) {
          this.to = this.currentPage * this.perPage
        } else {
          this.to = totalRows
        }
      }
    },
    editarActividad(item) {
      console.log('valores de un ROW: ', item)
    },
    actualizarNumerosTabla() {
      console.log('mensaje: Falta ver los numeros tabla')
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    eliminarActividad(value) {
      console.log('id_eliminar', value)
      this.$swal({
        title: `Esta seguro de eliminar la actividad "${value.nombre}"`,
        text: 'Esta accin es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('actividades_depor/deleteActividad', value.id).then(response => {
            console.log(response)
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Taller eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            store.dispatch('actividades_depor/fetchActividades', { id: this.id })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
  },
  setup() {
    const ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME = 'actividades_depor'

    // Register module
    if (!store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.registerModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME, actDeportivasStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)) store.unregisterModule(ACTIVIDADES_DEPOR_APP_STORE_MODULE_NAME)
    })

    const fields = [

      { key: 'nombre', label: 'Actividad', sortable: true },
      { key: 'fecha', label: 'Fecha', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
    }
  },
}
</script>
